import React, {useState , useEffect} from "react";
import { Table,Container } from "react-bootstrap";

function Cart({cartQuan , onIncrease, onDecrease, onDeleteData }) {
  
  const [ discountAlert , setDiscountAlert ] = useState(false)

  useEffect(()=>{
    setDiscountAlert(true)
  },[])

  return (
    <div>
      <Container>
      <Table responsive="md">
        <thead>
          <tr>            
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {cartQuan.map((a, i) => {
            return (
              <tr key={i}>               
                <td>{a.name}</td>
                <td>{a.price}</td>
                <td>{a.quan}</td>
                <td>
                  <button onClick={ ()=> { onIncrease(i) } } > + </button>
                  <button onClick={ ()=> { onDecrease(i) } } > - </button>
                </td>
                <td>{a.price * a.quan}</td>
                <td>
                  <button onClick={ ()=> { onDeleteData(i) } }>  </button>
                </td>
              </tr>
            );
          })}
        </tbody>
      </Table>
      </Container>
      <div>
         
      </div>
  
      {
        discountAlert === true &&
        (<div className="my-alert2">
        <p>  20%  !</p>
        <button onClick={() => { setDiscountAlert(false) }} >  </button>
        </div>)
      }

    </div>
  );
}

export default Cart;

